/**
 *  paper 
 */
export const currentScale = ref(1);

/**
 *  slider 
 */
export const calcCurrentScale = computed({
  get() {
    return Number((currentScale.value * 100).toFixed(0));
  },
  set(value) {
    currentScale.value = value / 100;
  }
});

